import React from "react"
import { Grid, Card, useMediaQuery } from "@material-ui/core"
import { makeStyles, useTheme } from "@material-ui/core/styles"
import ExampleImage from "../DataGatsbyImageBanner"
import AniLink from "gatsby-plugin-transition-link/AniLink";
import clsx from "clsx"

const useStyles = makeStyles(({ palette, ...theme }) => ({
  card: {
    transition: "all 400ms ease-in-out",
    borderRadius: 100,
    "& .card-icon": {
      position: "absolute",
      fontSize: 200,
      height: 200,
      width: 200,
      left: -80,
      bottom: -80,
      zIndex: 1,
      opacity: 0.24,
      transition: "all 400ms ease-in-out",
    },

    "&:hover": {
      transform: "translateY(-8px)",

      "& .card-icon": {
        transform: "translateY(-8px)",
        color: palette.secondary.main,
        opacity: 0.6,
      },
    },
  },
  containerMovil:{
    display:'flex',
    justifyContent:'center',
    flexWrap:'wrap'
  },
  containerMovilCard:{
    borderRadius:'100%',
    width:'100px',
    marginRight:'3px',
    marginLeft:'3px'
  },
  cardMovil:{
    borderRadius:100
  }
}))

const ImagenesSegmentos = ({ SegmentosImagen }) => {
  const classes = useStyles()
  const theme = useTheme()
  const isMobile = useMediaQuery(theme.breakpoints.down("xs"))

  return (
    <div className="section section-service4" id="service4">
      <div className="container">
        <Grid container spacing={9}>
          {SegmentosImagen.map((card, index) => (
            <Grid  item key={index}>
              <AniLink
                ip
                hex="#075182"
                to={`/${card.SegmentoAux}/`}
                paintDrip
                duration={0.3}
                direction="up"
              >
                <Card
                  elevation={0}
                  className={clsx("relative overflow-hidden card", classes.card)}
                >
                  <ExampleImage image={card.Imagen} width={isMobile ? 135 : 230} height={isMobile ? 135 : 230} />
                </Card>
              </AniLink>
              <div className="max-w-10 mb-1 mx-auto text-center">
                <h4  className={clsx({
                  "mt-0 font-normal text-1 text-primary": !isMobile,
                  "mt-0 font-normal text-12 text-primary": isMobile,
                })}>
                  {card.NombreSegmento}
                </h4>
              </div>
            </Grid>
          ))}
        </Grid>
      </div>
    </div>
  )
}

export default ImagenesSegmentos
